import React, { Component } from 'react';

export default class Footer extends Component{
  constructor(){
    super();
    this.state = {
      selected: false,
      imageChosen: false
    }
    this.internButton = this.internButton.bind(this)
    this.gitButton = this.gitButton.bind(this)
  }
  internButton(event){
    event.preventDefault()
    this.setState({ selected: !this.state.selected })
  }
  gitButton(event){
    event.preventDefault()
    this.setState({ imageChosen: !this.state.imageChosen })
  }


  render(){
    var chosen = null;
    if(this.state.selected){
      chosen = <div><p>This is our Intern Montage</p><iframe width="420" height="345" src="https://www.youtube.com/embed/0WCgg26uML8?autoplay=1"></iframe></div>
    }
    else{
      chosen = null
    }

    var gitImage = null;
    if(this.state.imageChosen){
      gitImage = <div><p>Please be patient, the image does takes a moment to load. Credit to Rushi <a href = "github.com/2016rshah">"2016rshah"</a> Shah for creating this package.</p><img src="http://ghchart.rshah.org/copperstick6" alt="copperstick6's Github chart" /></div>
    }
    else{
      gitImage = null;
    }
    return(
      <div>
      <h2>About Me.</h2>
              <img src="http://www.cs.utexas.edu/~copperst/self.jpg" alt="Dis me" width="300" height="300"/>
              <br />
              <br />
              <br />
              <p>My name is William Han. I'm currently a Sophomore at The University of Texas at Austin.</p>
              <p>I'm passionate about Backend Architecture (specifically Distributed Systems) and Machine Learning (General Principles and Neural Networks).</p>
              <p>I'm also passionate about entrepreneurship and creating stuff. I regularly attend hackathons, so you might be able to catch me there if you come!</p>
              <p>Outside of school and work stuff, I love to cook and do potlucks! Making people happy makes me happy by extention!</p>
              <br />
              <p>------------------------------------------------------------------------</p>
              <p>Here's a few other things about me:</p>
              <p>I'm a tech organizer at <a href = "https://freetailhackers.com/">Freetail Hackers</a></p>
              <p>I'm the Logistics Officer for <a href = "https://www.isss.io/">ISSS</a></p>
              <p>I'm also the Logistics Officer for <a href = "https://www.txcsmad.com/">MAD</a></p>
              <p>I'm the Engineering Lead for <a href = "http://www.cs.utexas.edu/~roadshow/">Texas Roadshow</a></p>
              <p>I interned for <a href onClick = {this.internButton}>Honeywell</a> in Summer 2017</p>
              {chosen}
              <p>I also commit frequently on <a href onClick = {this.gitButton}>Github</a></p>
              {gitImage}
              < br/>
              < br/>
      </div>
    )
  }

}



