import React, { Component } from 'react';
var moment = require('moment');

export default class Footer extends Component{
  constructor(){
    super()
    this.state = {
      year: null,
      lastUpdate: null
    }
  }
  componentDidMount(){
    this.setState({year: moment().get('year')})
  }
  render(){
    return(
      <div>
      <p>Copyright &copy; William 'copperstick6' Han {this.state.year}</p>
      <p>This website was built using ReactJS and Bootstrap, and served with Python's Flask framework.</p>
      </div>
    )
  }

}



